@import url(https://fonts.googleapis.com/css2?family=Neucha&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cabin+Sketch&display=swap);
body{margin:0;font-family:'Neucha', cursive}h1{font-size:6vw;font-family:'Cabin Sketch', cursive;font-weight:400;margin:10px}h2{font-size:36px;font-family:'Cabin Sketch', cursive;font-weight:400;margin:10px}

.App-header{align-items:center;display:flex;flex-direction:column;font-size:calc(10px + 2vmin);justify-content:center;min-height:100vh;text-align:center}.scroll-down{text-align:center;position:absolute;bottom:5%;width:95%}.scroll-down-icon{height:40px;width:40px}

.collapsable{border:2px solid #333;border-radius:255px 25px 225px 25px/25px 225px 25px 255px;font-size:20px;font-weight:500}

.page-navbar .nav-item :hover{background-color:#273036;border-radius:255px 25px 225px 25px/25px 225px 25px 255px;color:#fff}.page-navbar .nav-link{color:#273036;text-decoration:none}

.footer{background-color:#273036;height:100%;text-align:center}.footer .brand-name{color:#fff;font-size:42px;font-family:'Cabin Sketch', cursive;font-weight:400}

